import React,{useState} from 'react';
import './App.css';
import { Navbar } from '../../Components/Navbar';
import { Login } from '../Login/Login';
import { Register } from '../Register';
import {Landing} from '../Landing'

export const App =()=> {
  const [pageName, setPageName]=useState('landing')

  const nav =[
    {name:'landing'},
    {name:'login'},
    {name:'register'},
  ]
  return (
    <div className="App">
      <Navbar onClick={(element)=>setPageName(element)} nav={nav} />
      {pageName==='landing'&&<Landing />}
      {pageName==='login'&&<Login />}
      {pageName==='register'&&<Register />}
      
    </div>
  );
}
