import React from 'react';
import './Navbar.css'
export const Navbar =({nav,onClick})=>{
    console.log(nav)
    return (
        <div className='navbar'>
                {nav.map(({name})=>(
                    <button key={name}className='navbar-button' onClick={()=>onClick(name)}>{name}</button>
                ))}
        </div>
    )
}